//---------------------------
// ANNOTATION-FORM.ts
//---------------------------
import { Component, Input } from '@angular/core';
import { Icon } from '../../shared-interfaces';

type SelectItemOption = {
  label: string;
  value: string;
  selected?: boolean;
};

type SelectItemDivider = {
  divider: boolean;
};

/**
 * Interface for AnnotationFormComponent's "data"
 *
 * @property select (required)
 * - label (required)
 * - toggleLabel (required)
 * - items (required)
 * - payload (required)
 * - icon (optional)
 * - classes (optional)
 * @property actions (required)
 * - label (required)
 * - payload (required)
 * - classes (optional)
 * - icon (optional)
 * @property textarea (optional)
 * - value (required)
 * - payload (required)
 * - classes (optional)
 */
export interface AnnotationFormData {
  select: {
    label: string;
    toggleLabel: string;
    items: (SelectItemOption | SelectItemDivider)[];
    payload: any;
    icon?: Icon;
    classes?: string;
  };
  actions: {
    label: string;
    payload: any;
    classes?: any;
    icon?: Icon;
  }[];
  textarea?: {
    value: string | null;
    payload: any;
    classes?: string;
  };
}

@Component({
  selector: 'n7-annotation-form',
  templateUrl: './annotation-form.html'
})
export class AnnotationFormComponent {
  @Input() data: AnnotationFormData;

  @Input() emit: any;

  onClick(payload) {
    if (!this.emit) {
      return;
    }

    this.emit('click', payload);
  }

  onChange(inputPayload, inputValue) {
    if (!this.emit) {
      return;
    }

    this.emit('change', { inputPayload, inputValue });
  }
}
